      "description": "<p>We will also broadcast the event live for people who would rather stay at home on YouTube: <a href=\"https://youtu.be/JYnYmkfO4PU\" class=\"embedded\">https://youtu.be/JYnYmkfO4PU</a></p> <p>How to find the new venue:<br/>Enter the Nove Building from Luise-Ullrich-Straße 14 (you should see a statue and a restaurant outside). On entering you will see the main reception in front of you with a security guard, two lifts to the left and a model of the building to the right. Walk toward the reception, take the first left and walk toward the other two lifts in front of you. The first door on the left with the Reply logo leads to the Meetup on the *ground floor*.</p> <p>Agenda:</p> <p>18:30: Brotzeit<br/>19:00: Markus Jung (OSRAM) - The OSRAM portfolio of IoT connected products<br/>19:30: Christopher Ziegler (Actyx.io) - The modern way of automating and digitizing factories<br/>20:00: Networking</p> <p>Food and drink served throughout. All presentations in English.</p> ",
