      "link": "https://www.meetup.com/Holzkoepfchen/events/272453545/",
      "description": "<p>Bei diesem besonderen Event bauen wir Dein eigenes Weinregal und verkosten fünf charaktervolle und spannende Naturweine. Gemeinsam mit netten Leuten und in entspannter Atmosphäre werdet ihr handwerklich kreativ und entdeckt ungeahnte Geschmäcker atypischer Weinstile.</p> <p>Was erwartet Euch:</p> <p>Teil 1: Wir bauen Euer Weinregal und werden handwerklich kreativ: mit Holz, Kupfer, Metall und Filz werden wir schleifen, schneiden, lochen, schrauben, ölen uvm. Nebenbei lernt Ihr etwas über unsere heimischen Hölzer und deren Verarbeitung. Weitere Informationen zu unseren Weinregalen findet Ihr unter holzkoepfchen.de.</p> <p>Teil 2: Verkostung von spannenden &amp; charaktervollen Naturweinen. Hier lernt Ihr spannende Details zu Naturweinen, ihrer Philosophie sowie deren Herstellung. Wir degustieren eine Reihe einzigartiger Naturweine, von Weiß-, Rot- über Rosé- bis hin zu Orange-Weinen. Ob trüb oder ungeschwefelt, es erwarten Euch eigenwillige Weinstile, die Euch mit neuartigen Geschmackserlebnissen überraschen werden.</p> <p>Das Event, inkl. Verkostung von fünf Naturweinen und Weinregal-Bau kostet wie folgt:<br/>98,00 € Weinregal Volando<br/>108,00 € Weinregal Dado<br/>176,00 € Weinregal Bodeguita</p> <p>Anmeldung im Laden oder online unter: www.holzkoepfchen.de/kurse</p> <p>Material und Werkzeug sind inklusive. Es sind keine Vorkenntnisse für den Baukurs notwendig. Die Teilnehmerzahl ist auf 7 Personen limitiert.</p> <p>Wir freuen uns auf Euch-<br/>Max von Zero Dosage &amp;<br/>Tom von Holzköpfchen</p> ",
