      "link": "https://www.meetup.com/ImprovSTAR/events/271771147/",
      "description": "<p>Newest workshop: GET IN TOUCH!</p> <p>Do we pay attention to touch? Is bodily sensation a part of your improv or are you just a talking head? In this workshop, we will get in touch with our bodies, get in touch with our partners, and add a new layer to our Improvisation.<br/>Learn how to:<br/>Allow physical touch to resonate in your own body<br/>Let touch affect you and your character<br/>Expand your awareness of touch and physical proximity</p> <p>Min Level: Intermediate/Advanced<br/>Max Sts: 16</p> <p>Noah Levin hails from Ketchum, Idaho, USA and grew up performing theatre in schools and summer camps until earning a degree in Theatre and Education from the University of California in San Diego. Noah taught and performed improv, theatre and stand up comedy, and received formal improv training, at the famous PIT and UCB theatres in New York. Living in Barcelona since 2011, Noah has rapidly expanded his interests performing, teaching, and organizing BIG (Barcelona Improv Festival) as well as the European improv collective Ohana. He also acts in plays, produces live comedy shows, hosts a weekly radio show (Nick and Noah Need Help), and travels around Europe teaching and performing improv.</p> <p>For more info visit",
