      "link": "https://www.meetup.com/startups-munich/events/269005331/",
      "description": "<p>As you know Munich has become a vibrant startup hub with a lot of different players. So we decided it is time to build a meetup that brings together all Munich startup ecosystem players, entrepreneurs, investors, policymakers, education leaders, corporates and other stakeholders in one place once every 6 weeks for knowledge exchange, meaningful connections, networking, and recent community updates.</p> <p>How the meetup will look like:</p> <p>We start with Munich ecosystem news, like exits, exciting research, innovative breakthrough, investment rounds, impactful ideas and actions (around 20 min)</p> <p>The newsstand is followed by 3 startup pitches from local startups (3 min each pitch). Everybody is welcome to apply for the pitch spot, even if you have only an idea.</p> <p>We continue our evening with the speaker discussion about the meetup topic (around 30 min)</p> <p>As a closing remark, we will have an open mic for community members questions and “asks” and will help the presenter to get matched with relevant people to answer their questions (15 min)</p> <p>…… and then favorite networking with some healthy and not that healthy drinks!</p> <p>The topic for the second meetup: #fundraising.</p> <p>Learn from the best, build new and strong collaborations and become an active part of your city’s ecosystem! This event is absolutely for everybody who would like to be involved in the startup community development.</p> ",
